@import '~antd/dist/antd.css';
body {
  margin: 0;
  padding: 0;
  color: #333;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body p {
  margin-bottom: 0;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.w {
  margin: 0 auto;
  width: 1200px;
}
.m-b-20 {
  margin-bottom: 20px;
}
.m-b-50 {
  margin-bottom: 50px;
}
.m-b-100 {
  margin-bottom: 100px;
}
.t-a-center {
  text-align: center;
}
.home-page .home-header {
  padding: 10px 0;
  font-size: 30px;
}
.home-page .home-banner {
  height: 400px;
}
.home-page .home-banner .ant-carousel .slick-slide {
  height: 400px;
}
.home-page .home-banner .ant-carousel .slick-slide .carousel-pic {
  position: relative;
  height: 400px;
  background-size: 100%;
  background-position-y: -250px;
}
.home-page .home-banner .ant-carousel .slick-slide .carousel-pic .carousel-pic-title {
  position: absolute;
  top: 100px;
  width: 100%;
  text-align: center;
  color: #105274;
}
.home-page .home-banner .ant-carousel .slick-slide .carousel-pic .carousel-pic-title h1 {
  font-size: 56px;
  color: inherit;
}
.home-page .home-banner .ant-carousel .slick-slide .carousel-pic .carousel-pic-title p {
  font-size: 24px;
}
.home-page .home-nav .w {
  background-color: #f1f4fa;
}
.home-page .home-nav .w a {
  display: inline-block;
  box-sizing: border-box;
  width: 25%;
  padding: 20px 0;
  color: #333;
  font-size: 18px;
  text-align: center;
  border-left: 1px solid #eee;
}
.home-page .home-nav .w a:nth-child(1) {
  border-left: 0;
}
.home-page .home-nav .w a:hover {
  color: #00a4ff;
  background-color: #f9fbff;
}
.home-page .home-news-header {
  position: relative;
  padding: 10px 0;
  color: #00a4ff;
}
.home-page .home-news-header > span {
  font-size: 28px;
}
.home-page .home-news-header a {
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -16px;
}
.home-page .home-news-content:after {
  content: '';
  display: block;
  clear: both;
}
.home-page .home-news-content-left {
  float: left;
  width: 520px;
}
.home-page .home-news-content-left .ant-carousel .slick-slide {
  height: 356px;
}
.home-page .home-news-content-left .ant-carousel .slick-slide .carousel-pic {
  height: 356px;
  background-size: 100%;
}
.home-page .home-news-content-right {
  float: right;
  width: 680px;
}
.home-page .home-news-content-right .home-news-list {
  padding-left: 20px;
}
.home-page .home-news-content-right .home-news-list .ant-list-item {
  padding: 0;
  border: 0;
}
.home-page .home-news-content-right .home-news-list .ant-list-item .ant-list-item-main .ant-list-item-meta {
  margin-bottom: 0;
}
.home-page .home-news-content-right .home-news-list .ant-list-item .ant-list-item-main .ant-list-item-meta .ant-list-item-meta-title {
  max-width: 500px;
  color: #00a4ff;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.home-page .home-news-content-right .home-news-list .ant-list-item .ant-list-item-main .ant-list-item-content {
  text-indent: 2em;
}
.home-page .home-news-content-right .home-news-list .ant-list-item .ant-list-item-extra {
  text-align: right;
}
.home-page .home-link .home-link-list {
  margin: 0 auto;
  width: 1080px;
}
.home-page .home-link .home-link-list .home-link-item {
  display: inline-block;
  width: 25%;
  padding: 10px 0;
  font-size: 18px;
  text-align: center;
  color: #fff;
  background-color: #00a4ff;
}
.home-page .home-link .home-link-list .home-link-item:hover {
  background-color: #1F99E5;
}
.home-page .home-bottom {
  background-color: #f1f4fa;
}
.home-page .home-bottom .w {
  padding: 20px 0;
  text-align: center;
}
